/**
 * AI
 * API
 */

import { APIProvider } from './api_provider';
import { getApiUrl } from './common';

/**
 * AI
 */
export interface AIGenerateRequest {
  /** API Provider */
  provider: APIProvider;
  /** API */
  apiKey: string;
  /**  */
  prompt: string;
  /**  */
  onStream?: (chunk: string) => void;
  /**  0-20.7 */
  temperature?: number;
  /** token2000 */
  maxTokens?: number;
}

/**
 * 
 */
export interface BackendGenerateRequest {
  /** API Provider ID */
  provider_id: number;
  /**  */
  prompt: string;
  /**  0-20.7 */
  temperature?: number;
  /** token2000 */
  max_tokens?: number;
  /** true */
  stream?: boolean;
  /** Provider */
  model?: string;
}

/**
 * AI
 */
export interface AIGenerateResponse {
  /**  */
  content: string;
  /**  */
  success: boolean;
  /**  */
  error?: string;
}

/**
 * AI
 */
export class AIService {
  /**
   * 
   * @param request 
   * @returns Promise<AIGenerateResponse>
   */
  static async generate(request: AIGenerateRequest): Promise<AIGenerateResponse> {
    const { provider, apiKey, prompt, onStream, temperature = 0.7, maxTokens = 2000 } = request;

    try {
      // API
      if (provider.api_kind === 'Ollama') {
        return await this.generateWithOllama(provider, apiKey, prompt, onStream, temperature, maxTokens);
      } else {
        //  OpenAI 
        return await this.generateWithOpenAI(provider, apiKey, prompt, onStream, temperature, maxTokens);
      }
    } catch (error: any) {
      console.error('AI:', error);
      return {
        content: '',
        success: false,
        error: error.message || '',
      };
    }
  }

  /**
   *  OpenAI 
   */
  private static async generateWithOpenAI(
    provider: APIProvider,
    apiKey: string,
    prompt: string,
    onStream?: (chunk: string) => void,
    temperature: number = 0.7,
    maxTokens: number = 2000
  ): Promise<AIGenerateResponse> {
    const url = `${provider.api_url}/chat/completions`;
    
    const requestBody = {
      model: provider.api_model,
      messages: [
        {
          role: 'user',
          content: prompt,
        },
      ],
      temperature,
      max_tokens: maxTokens,
      stream: !!onStream, //  onStream 
    };

    const headers: HeadersInit = {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${apiKey}`,
    };

    try {
      const response = await fetch(url, {
        method: 'POST',
        headers,
        body: JSON.stringify(requestBody),
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.error?.message || `API: ${response.status}`);
      }

      // 
      if (onStream && response.body) {
        return await this.handleStreamResponse(response, onStream);
      }
      
      // 
      const data = await response.json();
      const content = data.choices?.[0]?.message?.content || '';
      
      return {
        content,
        success: true,
      };
    } catch (error: any) {
      throw error;
    }
  }

  /**
   *  Ollama 
   */
  private static async generateWithOllama(
    provider: APIProvider,
    apiKey: string,
    prompt: string,
    onStream?: (chunk: string) => void,
    temperature: number = 0.7,
    maxTokens: number = 2000
  ): Promise<AIGenerateResponse> {
    // Ollama  OpenAI 
    //  api_url  /v1 OpenAI 
    if (provider.api_url.includes('/v1')) {
      return await this.generateWithOpenAI(provider, apiKey, prompt, onStream, temperature, maxTokens);
    }

    //  Ollama 
    const url = `${provider.api_url}/api/generate`;
    
    const requestBody = {
      model: provider.api_model,
      prompt,
      temperature,
      stream: !!onStream,
    };

    try {
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestBody),
      });

      if (!response.ok) {
        throw new Error(`Ollama API: ${response.status}`);
      }

      // 
      if (onStream && response.body) {
        return await this.handleOllamaStreamResponse(response, onStream);
      }
      
      // 
      const data = await response.json();
      const content = data.response || '';
      
      return {
        content,
        success: true,
      };
    } catch (error: any) {
      throw error;
    }
  }

  /**
   *  OpenAI 
   */
  private static async handleStreamResponse(
    response: Response,
    onStream: (chunk: string) => void
  ): Promise<AIGenerateResponse> {
    const reader = response.body!.getReader();
    const decoder = new TextDecoder('utf-8');
    let fullContent = '';

    try {
      while (true) {
        const { done, value } = await reader.read();
        
        if (done) break;
        
        const chunk = decoder.decode(value, { stream: true });
        const lines = chunk.split('\n').filter(line => line.trim() !== '');
        
        for (const line of lines) {
          if (line.startsWith('data: ')) {
            const data = line.slice(6);
            
            if (data === '[DONE]') {
              continue;
            }
            
            try {
              const json = JSON.parse(data);
              const content = json.choices?.[0]?.delta?.content || '';
              
              if (content) {
                fullContent += content;
                onStream(content);
              }
            } catch (e) {
              // 
            }
          }
        }
      }
      
      return {
        content: fullContent,
        success: true,
      };
    } catch (error: any) {
      throw error;
    }
  }

  /**
   *  Ollama 
   */
  private static async handleOllamaStreamResponse(
    response: Response,
    onStream: (chunk: string) => void
  ): Promise<AIGenerateResponse> {
    const reader = response.body!.getReader();
    const decoder = new TextDecoder('utf-8');
    let fullContent = '';

    try {
      while (true) {
        const { done, value } = await reader.read();
        
        if (done) break;
        
        const chunk = decoder.decode(value, { stream: true });
        const lines = chunk.split('\n').filter(line => line.trim() !== '');
        
        for (const line of lines) {
          try {
            const json = JSON.parse(line);
            const content = json.response || '';
            
            if (content) {
              fullContent += content;
              onStream(content);
            }
            
            if (json.done) {
              break;
            }
          } catch (e) {
            // 
          }
        }
      }
      
      return {
        content: fullContent,
        success: true,
      };
    } catch (error: any) {
      throw error;
    }
  }

  /**
   * 
   * @param request 
   * @returns Promise<AIGenerateResponse>
   */
  static async generateViaBackend(
    providerId: number,
    prompt: string,
    onStream?: (chunk: string) => void,
    temperature: number = 0.7,
    maxTokens: number = 2000
  ): Promise<AIGenerateResponse> {
    try {
      // URL
      const url = getApiUrl('generate');
      
      // Token
      const token = localStorage.getItem('auth_token');
      if (!token) {
        throw new Error('');
      }

      // 
      const requestBody: BackendGenerateRequest = {
        provider_id: providerId,
        prompt,
        temperature,
        max_tokens: maxTokens,
        stream: !!onStream,
      };

      const headers: HeadersInit = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`,
      };

      const response = await fetch(url, {
        method: 'POST',
        headers,
        body: JSON.stringify(requestBody),
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.message || `API: ${response.status}`);
      }

      // 
      if (onStream && response.body) {
        return await this.handleBackendStreamResponse(response, onStream);
      }
      
      // 
      const data = await response.json();
      const content = data.data?.content || '';
      // HTMLXSS
      const cleanContent = content.replace(/<[^>]*>/g, '');
      
      return {
        content: cleanContent,
        success: true,
      };
    } catch (error: any) {
      console.error('AI:', error);
      return {
        content: '',
        success: false,
        error: error.message || '',
      };
    }
  }

  /**
   * 
   */
  private static async handleBackendStreamResponse(
    response: Response,
    onStream: (chunk: string) => void
  ): Promise<AIGenerateResponse> {
    const reader = response.body!.getReader();
    const decoder = new TextDecoder('utf-8');
    let fullContent = '';

    try {
      while (true) {
        const { done, value } = await reader.read();
        
        if (done) break;
        
        const chunk = decoder.decode(value, { stream: true });
        const lines = chunk.split('\n').filter(line => line.trim() !== '');
        
        for (const line of lines) {
          if (line.startsWith('data: ')) {
            const data = line.slice(6);
            
            try {
              const json = JSON.parse(data);
              
              // 
              if (json.error) {
                throw new Error(json.error);
              }
              
              // 
              if (json.done) {
                break;
              }
              
              // HTML
              const content = json.content || '';
              // HTMLXSS
              const cleanContent = content.replace(/<[^>]*>/g, '');
              if (cleanContent) {
                fullContent += cleanContent;
                onStream(cleanContent);
              }
            } catch (e) {
              // 
              console.warn('Failed to parse SSE data:', data, e);
            }
          }
        }
      }
      
      return {
        content: fullContent,
        success: true,
      };
    } catch (error: any) {
      throw error;
    }
  }
}
