import React, { useCallback, useEffect, useState } from 'react';
import { AIService, APIProviderService } from '../services';
import '../styles/app.css';
import Toast from './Toast';

/**
 * AIProps
 */
export interface AICreatingProps {
  /**  */
  visible: boolean;
  /**  */
  onClose: () => void;
  /** AI */
  title: string;
  /**  */
  promptTemplate: string;
  /**  */
  placeholders: Record<string, string>;
  /**  */
  onSuccess: (content: string) => void;
}

/**
 * AI
 */
type GenerationStatus = 'idle' | 'thinking' | 'generating' | 'success' | 'error';

/**
 * AICreating 
 * API
 * 
 * @component
 */
const AICreating: React.FC<AICreatingProps> = ({
  visible,
  onClose,
  title,
  promptTemplate,
  placeholders,
  onSuccess,
}) => {
  // 
  const [status, setStatus] = useState<GenerationStatus>('idle');
  
  // 
  const [generatedContent, setGeneratedContent] = useState('');
  
  // 
  const [errorMessage, setErrorMessage] = useState('');
  
  // Toast
  const [toast, setToast] = useState<{ message: string; type: 'success' | 'error' | 'warning' | 'info' } | null>(null);

  /**
   * Toast
   */
  const showToast = (message: string, type: 'success' | 'error' | 'warning' | 'info' = 'info') => {
    setToast({ message, type });
  };

  /**
   * 
   */
  const replacePlaceholders = (template: string, data: Record<string, string>): string => {
    let result = template;
    Object.entries(data).forEach(([key, value]) => {
      const placeholder = `{{${key}}}`;
      result = result.replace(new RegExp(placeholder, 'g'), value || '');
    });
    return result;
  };

  /**
   * API
   */
  const generateContent = useCallback(async () => {
    try {
      setStatus('thinking');
      setGeneratedContent('');
      setErrorMessage('');

      // 
      if (!promptTemplate || promptTemplate.trim() === '') {
        throw new Error('');
      }

      // 1. API
      const apiConfigs = await APIProviderService.getEnabled();
      
      if (!apiConfigs || apiConfigs.length === 0) {
        throw new Error('APIAPIProvider');
      }

      const apiConfig = apiConfigs[0]; // 

      // 2. 
      const finalPrompt = replacePlaceholders(promptTemplate, placeholders);

      console.log('API:', apiConfig.name);
      console.log(':', finalPrompt);

      setStatus('generating');

      // 3. 
      const result = await AIService.generateViaBackend(
        apiConfig.id,
        finalPrompt,
        (chunk: string) => {
          setGeneratedContent(prev => prev + chunk);
        },
        0.7,
        2000
      );

      if (!result.success) {
        throw new Error(result.error || 'API');
      }

      setStatus('success');
      
    } catch (error: any) {
      console.error('AI:', error);
      setStatus('error');
      setErrorMessage(error.message || '');
      showToast(error.message || '', 'error');
    }
  }, [promptTemplate, placeholders]);

  /**
   * 
   */
  const handleRegenerate = () => {
    generateContent();
  };

  /**
   *  - 
   */
  const handleConfirm = () => {
    if (generatedContent.trim()) {
      onSuccess(generatedContent);
      onClose();
    } else {
      showToast('', 'warning');
    }
  };

  /**
   * 
   */
  const handleCancel = () => {
    onClose();
  };

  /**
   * 
   */
  useEffect(() => {
    if (visible) {
      generateContent();
    }
  }, [visible, generateContent]);

  if (!visible) {
    return null;
  }

  return (
    <>
      {/* Toast  */}
      {toast && (
        <Toast
          message={toast.message}
          type={toast.type}
          onClose={() => setToast(null)}
        />
      )}

      {/*  */}
      <div className="fullscreen-modal">
        <div className="fullscreen-content">
          {/*  */}
          <div className="fullscreen-header">
            <h3>{title} - AI</h3>
            <button className="close-button" onClick={handleCancel}></button>
          </div>

          {/*  */}
          <div className="ai-creating-body">
            {/* Thinking */}
            {status === 'thinking' && (
              <div className="ai-thinking">
                <div className="thinking-spinner"></div>
                <p>...</p>
              </div>
            )}

            {/*  */}
            {status === 'generating' && (
              <div className="ai-generating">
                <div className="generating-indicator">
                  <span className="dot"></span>
                  <span className="dot"></span>
                  <span className="dot"></span>
                </div>
                <p>AI...</p>
              </div>
            )}

            {/*  */}
            {(status === 'generating' || status === 'success') && generatedContent && (
              <div className="ai-content">
                <textarea
                  className="fullscreen-textarea"
                  value={generatedContent}
                  onChange={(e) => setGeneratedContent(e.target.value)}
                  placeholder="AI..."
                  readOnly={status === 'generating'}
                />
              </div>
            )}

            {/*  */}
            {status === 'error' && (
              <div className="ai-error">
                <p className="error-icon"></p>
                <p className="error-text">{errorMessage}</p>
              </div>
            )}
          </div>

          {/*  */}
          <div className="fullscreen-footer">
            <div className="footer-left">
              <span className="char-count">{generatedContent.length} </span>
            </div>
            <div className="footer-right">
              {status === 'success' && (
                <button className="btn btn-secondary" onClick={handleRegenerate}>
                  
                </button>
              )}
              {status === 'error' && (
                <button className="btn btn-secondary" onClick={handleRegenerate}>
                  
                </button>
              )}
              <button 
                className="btn btn-primary" 
                onClick={handleConfirm}
                disabled={status === 'thinking' || status === 'generating' || !generatedContent.trim()}
              >
                
              </button>
              <button className="btn btn-secondary" onClick={handleCancel}>
                
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default AICreating;
